.tabs {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.tabs .tab-item {
  min-width: 88px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  padding: 20px 16px;
  color: var(--heading-color);
}

.tabs .tab-item::before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 2px;
  background: var(--primary-color);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.tabs .tab-item:active {
  background: white;
  position: relative;
  color: black;
}

.tabs .tab-item:active::before {
  opacity: 1;
}

table tbody tr td {
  font-weight: bold;
  font-size: 16px;
  height: 21px;
  padding: 1.6rem;
}